:root {
    --oc-menu-width: 320px;
    --oc-container-width: 1200px;
    --oc-nav-z-index: 4;
    --oc-overlay-z-index: 3;
    --oc-off-canvas-z-index: 5;
    --oc-navbar-bg-color: #212529;
    --oc-overlay-bg-color: rgba(0, 0, 0, 0.5);
    --oc-off-canvas-bg: #fff;
    --oc-off-canvas-offset-top: 20px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.oc-nav {
    background-color: var(--oc-navbar-bg-color);
    position: sticky;
    top: 0;
    z-index: var(--oc-nav-z-index);
}

.oc-nav__container {
    max-width: var(--oc-container-width);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.oc-nav__brand {
    color: white;
    text-decoration: none;
    font-size: 1.25rem;
    font-weight: bold;
}

.oc-nav__toggle {
    display: block;
    background: none;
}

.oc-nav__menu {
    position: fixed;
    top: var(--oc-off-canvas-offset-top);
    right: 100%;
    width: var(--oc-menu-width);
    height: calc(100% - var(--oc-off-canvas-offset-top));
    background: var(--oc-off-canvas-bg);
    z-index: var(--oc-off-canvas-z-index);
    transition: transform 450ms cubic-bezier(.19, 1, .22, 1);
    flex-direction: column;
    align-items: stretch;
    padding: 1rem;
    overflow-y: auto;
    
    &.show {
        transform: translateX(var(--oc-menu-width));
    }

    &.oc-nav__menu--right {
        right: auto;
        left: 100%;

        &.show {
            transform: translateX(calc(-1 * var(--oc-menu-width)));
        }
    }
}

.oc-nav__nav {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    flex-direction: column;
    gap: 0;
    width: 100%;
}

.oc-nav__nav-item {
    margin-bottom: 0.5rem;
    width: 100%;
}

.oc-nav__nav-link {
    color: #212529;
    text-decoration: none;
    padding: 0.75rem 0;
    border-bottom: 1px solid #f8f9fa;
    transition: color 0.3s ease;
    display: block;
    cursor: pointer;
    width: 100%;
}

.oc-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--oc-overlay-bg-color);
    z-index: var(--oc-overlay-z-index);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    
    &.show {
        opacity: 1;
        visibility: visible;
    }
}

.oc-nav__close {
    display: flex;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #000;
    padding: 0;
    width: 24px;
    height: 24px;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    align-self: flex-end;
}

@media (min-width: 992px) {
    .oc-nav__toggle,
    .oc-overlay,
    .oc-nav__close {
        display: none;
    }

    .oc-nav__menu {
        display: flex;
        position: static;
        background: transparent;
        width: auto;
        height: auto;
        transform: none;
        flex-direction: row;
        padding: 0;
        align-items: center;
        gap: 2rem;
        overflow: visible;
    }

    .oc-nav__nav {
        flex-direction: row;
        gap: 2rem;
        align-items: center;
        width: auto;
    }

    .oc-nav__nav-item {
        margin-bottom: 0;
        width: auto;
    }

    .oc-nav__nav-link {
        border-bottom: none;
        padding: 0.5rem 0;
        width: auto;
    }

    .oc-nav__nav-link:hover,
    .oc-nav__nav-link.active {
        color: white;
    }
}
